.member{
  width: 100%;
  line-height: 1.5;
  padding: 0 10px;
  .member-title{
    @include flexBox(row, center, flex-start);
    div{
      @include size(4px,35px);
      background: $lightBrown;
      margin: 20px 10px 20px 0;
    }
  }
  .member-page{
    @include pageAsideMenu;
    .member-list{
      @include size(78%, 100%);
      min-height: 760px;
      overflow: scroll;
      border: 2px solid $middleGray;
      background: #fff;
    }
  }
}
.homepage{
  width: 100%;
  padding: 2rem 2.5rem;
  color: #000;
  .pi-pencil, .pi-user{
    border: 1px solid #000;
    border-radius: 100%;
    padding: 4px;
  }
  .pi-pencil{
    padding: 8px;
  }
  .member-level{
    margin-left: 10px;
    background: #C7B299;
    padding: 2px 20px;
    border-radius: 5px;
    font-size: 12px;
  }
  .member-edit{
    margin-left: 48px;
    span{
      @include text($darkGray,12,normal);
    }
    p{
      color: $darkGray;
    }
    .member-detail{
      span{
        color: red;
      }
    }
    .member-detail, .member-detail_exception{
      @include flexBox(row, center, flex-start);
      margin-top: 15px;
      p{
        width: 85px;
        color: #000;
      }
      input{
        width: 80%;
      }
      .field-checkbox{
        width: 100px;
      }
    }
  }
  button{
    @include size(48%, 40px);
    margin: 10px auto;
  }
  
}
.order{
  @include size(100%, 760px);
  .orderData{
    height: 300px;
    .goods_detail{
      img{
        width: 20%;
      }
    }
  }
  .p-datatable{
    height: 100%;
    min-height: 760px;
    @include flexBox(column, flex-start, flex-start);
    .p-datatable-wrapper{
      @include size(100%, 800px);
      .p-button-sm{
        padding: 0.2rem 0.5rem;
      }
    }
    .p-paginator-bottom{
     width: 100%; 
    }
  }
  &-media{
    display: none;
    border-bottom: 1px solid $middleGray;
    h2{
      @include text(#000, 18,normal);
      background: rgb(226, 226, 226);
      padding: 10px;
    }
    ul{
      padding: 10px;
    }
    .p-button.p-button-sm{
      padding: 5px;
    }
    button{
      margin-right: 20px;
      width: 50px;
    }
  }
  &-dialog{
    &-data{
      max-height: 300px;
      overflow: scroll;
      overflow-x: hidden;
      /*  */
      &::-webkit-scrollbar {
        width: 8px;
      }
      /*  */
      &::-webkit-scrollbar-track {
        background: none;
      }
      /*  */
      &::-webkit-scrollbar-thumb {
        background: #e4c9ac;
        border-radius: 10px;
      }
      .goods_detail{
        @include flexBox(row, center, flex-start);
        margin: 20px 0 20px 17px;
        img{
          width: 20%;
        }
        h3{
          font-size: 16px;
          margin-bottom: 10px;
        }
        p{
          @include text($darkGray,14, normal);
        }
        span{
          font-size: 20px;
          margin-left: 5px;
        }
      }
    }
    &-delivery{
      .method{
        border: 1px solid $middleGray;
        p{
          width: 100px;
          background: #f4e6d6;
          padding: 7px;
          border-right:1px solid $middleGray;
        }
        span{
          padding: 7px;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
    }
    .data_title{
      @include flexBox(row, center, flex-start);
      margin-left: 5px;
      div{
        @include size(4px, 30px);
        background: $lightBrown;
        margin-right: 8px;
      }
      p{
        color: $middleBrown;
      }
    }
  }
}
.discount{
  padding: 30px;
  p{
    @include text($middleGray, 20, normal);
    text-align: center;
  }
  &-data{
    border: 1px solid #C7B299;
    padding: 20px;
    margin-bottom: 30px;
    h2{
      position: relative;
    }
    p{
      @include text($lightBrown, 24, normal);
      margin-bottom: 10px;
      text-align: left;
    }
    div{
      @include size(100%, 30px);
      border: 1px solid #C7B299;
      border-radius: 5px;
      margin-bottom: 15px;
      position: relative;
      &::before{
        content: '';
        @include triangle(9px,4px,0,4px,$darkGray transparent transparent transparent);
        @include position(-9px,49.5%,'','');
      }
      &::after{
        content: '';
        @include position(-27px,46.5%,'','');
        font-size: 10px;
      }
      span{
        height: 29px;
        display: block;
        border-radius: 4px;
        background: #C7B299;
        &::before{
          content: '';
          @include triangle(9px,4px,0,4px,$darkGray transparent transparent transparent);
          @include position(-8px,-4px,'','');
          z-index: 1;
        }
        &::after{
          content: '';
          @include position(-27px,-4px,'','');
          @include text($darkGray, 10, normal);
        }
      }
    }
  }  
  &-coupon{
    @include flexBox(row, center, center);
    margin-bottom: 20px;
    &_header{
      @include size(80px,140px);
      position: relative;
      background: linear-gradient(90deg, transparent 0, transparent 0.1875rem, #e9d7c0 0);
      span {
        @include size(10px,calc(100% - 10px));
        @include position(0.3rem,'','',0);
        background: radial-gradient(circle at 0 0.25rem, transparent 0, transparent 0.1875rem, #e9d7c0 0);
        background-size: 0.25rem 0.625rem;
        &::before {
          content: '';
          @include size(5px,5px);
          @include position(-5px,'','',0);
          background-color: #e9d7c0;
        }
        &::after {
          content: '';
          @include size(5px,5px);
          @include position('','',-5px,0);
          background-color: #e9d7c0;
        }
      }
    }
    &_content{
      height: 140px;
      border: 1px solid #e9d7c0;
      padding: 10px;
      @include flexBox(column, center, center);
      h2{
        @include text($lightBrown, 24, normal);
      }
      p{
        font-size: 16px;
      }
    }
  }
}
.like{
  &-notice{
    @include flexBox(row, center, center);
    button{
      margin-left: 10px;
      padding: 5px;
    }
  }
  .likeList{
    @include flexBox(row, center, space-between);
    padding: 20px 40px 30px 30px;
    border-bottom: 1px solid $darkGray;
    height: 100%;
    cursor: pointer;
    &:hover{
      h2, p{
        color: $lightBrown;
      }
    }
    img{
      width: 150px;
    }
    .iconList{
      @include flexBox(column, center, flex-start);
      width: 80px;
      cursor: pointer;
      &:hover{
        color: #000;
      }
      i{
        border: 1px solid $darkGray;
        padding: 5px;
        border-radius: 5px;
      }
    }
    &_text{
      @include size(100%, 133px);
      @include flexBox(column,flex-start,center);
      p{
        @include text($middleBrown,18, 600);
      }
    }
  }
}

@media(max-width: 960px) {
  .member{
    .member-page{
      .member-list{
        width: 95%;
        margin: auto;
      }
    }
  }
  .homepage{
    .member-edit{
      .member-detail{
        @include flexBox(column, flex-start, flex-start);
        margin-top: 15px;
        p{
          width: 85px;
          color: #000;
        }
        input{
          width: 100%;
        }
        .field-checkbox{
          width: 100px;
        }
      }
    }
  }
  .discount{
    &-coupon{
      width: 48%;
      min-width: 270px;
      &_content{
        width: 100%; 
        h2{
          @include text($lightBrown, 24, normal);
        }
        p{
          font-size: 13px;
        }
      }
    }
  }
}
@media(max-width: 700px) {
  .order{
    .p-datatable{
      display: none;
    }
    &-media{
      display: block;
    }
  }
  .discount{
    &-coupon{
      width: 100%;
    }
  }
}
@media(max-width: 570px) {
  .homepage{
    .member-edit{
      margin-left: 0;
      .member-detail_exception{
        p{
          width: 65px;
        }
      }
      button{
        font-size: 16px;
      }
    }
  }
  .like{
    .likeList{
      padding-top: 5px;
      padding-right: 10px;
      img{
        width: 100px;
        margin-right: 10px;
      }
      span{
        display: none;
      }
    }
  }
  .discount{
    padding: 15px;
    &-data{
      padding: 10px;
      h2{
        font-size: 18px;
      }
      p{
        font-size: 16px;
        margin-bottom: 30px;
      }
      span{
        font-size: 14px;
      }
    } 
  }
}